.sidebarLeft {
  width: var(--sidebar-width);
  height: 100vh;
  position: fixed;
  left: 0;
  background-color: var(--main-bg);
  padding: 30px 0 20px 30px;
  color: var(--active-txt-color);
  border-right: 1px solid var(--border-color);
  .sidebarLeft__logo {
    display: flex;
    align-items: center;
    .logo {
      font-size: 25px;
      font-weight: 600;
    }
    i {
      color: var(--main-color);
      margin-right: 10px;
      font-size: 40px;
    }
  }

  .sidebarLeft__content {
  }
}
