.sidebarRightItem {
  padding: 10px 0;
  &__title {
    margin: 20px 0;
    font-weight: 500;
    color: var(--active-txt-color);
  }
  &__content {
    display: flex;
    align-items: flex-start;
    flex-direction: column;
    .content__item {
      display: flex;
      margin: 10px 0;
      align-items: stretch;
      .content__item-img {
        margin-right: 20px;
        width: 70px;
        height: auto;
        object-fit: cover;
        border-radius: 10px;
      }
      .content__item-info {
        display: flex;
        flex-direction: column;
        justify-content: space-around;
        p {
          color: var(--active-txt-color);
          font-weight: 500;
          font-size: 16px;
        }
        .content__item-info-div {
          button {
            outline: none;
            border: none;
            border-radius: 5px;
            background-color: yellow;
            color: black;
            font-weight: 700;
            padding: 3px 3px;
            margin-right: 10px;
          }
          span {
            color: var(--active-txt-color);
          }
        }
      }
    }
  }
}
