html {
  --main-bg: #ffffff;
  --txt-color-one: #aaabbe;
  --txt-color-two: #e1e0e5;

  --setting-bg: #3e3e3e0d;
  --active-txt-color: #4b4b58;
  --main-color: #349eff;
  --border-color: #e8e8e8;
  --second-color: #62b4ff;
  --box-shadow: rgba(149, 157, 165, 0.2) 0px 8px 24px;

  --main-bg-light: #ffffff;
  --border-color-light: #e8e8e8;
  --setting-bg-light: #3e3e3e0d;

  --txt-color-light-one: #aaabbe;
  --txt-color-light-two: #e1e0e5;
  --active-txt-color-light: #4b4b58;
  --box-shadow-light: rgba(149, 157, 165, 0.2) 0px 8px 24px;

  --main-bg-dark: #191a1f;
  --setting-bg-dark: #3e3e3e3b;

  --border-color-dark: #2a2a2b;
  --txt-color-dark-one: #62636c;
  --txt-color-dark-two: #9696a2;
  --active-txt-color-dark: #ffffff;
  --box-shadow-dark: rgba(0, 0, 0, 0.2) 0px 5px 10px;

  --main-color-blue: #349eff;
  --second-color-blue: #62b4ff;

  --main-color-red: #fb0b12;
  --second-color-red: #ff4a6b;

  --main-color-cyan: #10d4d2;
  --second-color-cyan: #2ae9e6;

  --main-color-green: #019707;
  --second-color-green: #4caf50;

  --main-color-orange: #d68102;
  --second-color-orange: #fca11a;

  --sidebar-width: 250px;
  --border-radius: 30px;
  --topnav-height: 40px;
  --transition-cubic: cubic-bezier(0.175, 0.885, 0.32, 1.275);
}
* {
  box-sizing: border-box;
  padding: 0;
  margin: 0;
}
a {
  text-decoration: none;
}

p {
  padding: 0;
  margin: 0;
}
ul {
  list-style: none;
  padding: 0;
}
//layout
.layout__content {
  margin: 0 var(--sidebar-width);
  background-color: var(--main-bg);
  min-height: 100vh;
  padding: 30px 50px 30px 50px;
}
@media screen and (max-width: 1024px) {
  .layout__content {
    margin: 0 0 0 var(--sidebar-width);
  }
}
.slick-next:before,
.slick-prev:before {
  font-size: 20px;
  line-height: 1;
  opacity: 0.75;
  color: var(--main-color);
}
