.setting {
  margin-top: 50px;
  color: var(--txt-color-two);
  .setting__title {
    font-size: 40px;
    font-weight: 600;
    color: var(--active-txt-color);
  }
  &__mode {
    margin: 30px 0;
    &-title {
      font-size: 25px;
      color: var(--active-txt-color);
    }
    &-items {
      display: grid;
      grid-template-columns: 1fr 1fr;
      margin-top: 30px;
    }
  }
  &__color {
    margin: 30px 0;
    &-title {
      color: var(--active-txt-color);

      font-size: 25px;
    }
    &-items {
      display: grid;
      margin-top: 30px;
      grid-template-columns: 1fr 1fr 1fr 1fr;
    }
  }
}

// item
.item {
  display: flex;
  margin: 0 5px;
  border-radius: 12px;
  padding: 20px;
  position: relative;
  background: var(--setting-bg);
  flex-direction: column;
  height: 120px;
  cursor: pointer;
  justify-content: space-between;
  align-items: center;
  transition: all 0.25s linear;
  border: 0px solid var(--main-color);
  p {
    color: var(--txt-color-one);
  }
  i {
    position: absolute;
    // background-color: yellow;
    top: 10px;
    color: var(--main-color);
    font-size: 20px;
    right: 10px;
    border-radius: 50%;
    opacity: 0;
    transition: 0.25s all linear;
  }
  .item-color {
    width: 40px;
    height: 40px;
    border-radius: 50%;
  }
}
.item:hover {
  border: 2px solid var(--main-color);
  -webkit-box-shadow: 0px 0px 10px 0px var(--main-color);
  box-shadow: 0px 0px 10px 0px var(--main-color);
}
.item:hover i {
  opacity: 1;
}
// Background
.light-background {
  background-color: var(--main-bg-light);
  color: #000;
}

.dark-background {
  background-color: black;
  color: #fff;
}
.blue-color {
  background-color: var(--main-color-blue);
  color: #fff;
}

.red-color {
  background-color: var(--main-color-red);
  color: #fff;
}

.cyan-color {
  background-color: var(--main-color-cyan);
  color: #fff;
}

.green-color {
  background-color: var(--main-color-green);
  color: #fff;
}

.orange-color {
