.sidebarRight {
  width: var(--sidebar-width);
  height: 100vh;
  position: fixed;
  right: 0;
  top: 0;
  background-color: var(--main-bg);
  padding: 30px 30px 30px 30px;
  border-left: 1px solid var(--border-color);

  .sidebarRight__search {
    position: relative;
    color: var(--txt-color-one);
    input {
      color: var(--txt-color-one);
      padding: 10px 20px 10px 50px;
      width: 100%;
      outline: none;
      background-color: var(--main-bg);
      border-radius: 16px;
      font-size: 18px;
      border: 1px solid var(--txt-color-one);
    }
    i {
      position: absolute;
      top: 23%;
      left: 20px;
      font-size: 25px;
    }
  }
  .sidebarRight__btn {
    padding: 10px 20px;
    width: 100%;
    border: none;
    background: var(--main-color);
    color: white;
    font-size: 18px;
    cursor: pointer;
    border-radius: 10px;
  }
}
@media screen and (max-width: 1024px) {
  .sidebarRight {
    display: none;
  }
}
