.banners {
  position: relative;
  .slick-next::before,
  .slick-prev::before {
    font-size: 40px;
  }
  .slick-prev::before {
    display: none;
  }
  .slick-next {
    right: 50px;
  }
}
.banner {
  width: 100%;
  height: 400px;
  padding: 50px;
  border-radius: var(--border-radius);
  color: white;
  display: flex;
  align-items: flex-end;
  position: relative;
  .banner__contents {
    width: 60%;
    h1 {
      font-size: 30px;
      text-shadow: 0px 0px 10px black;
    }
    p {
      padding: 10px 0;
      text-shadow: 0px 0px 10px black;
    }

    .banner__contents-buttons {
      display: flex;
      align-items: center;
      .btn {
        cursor: pointer;
        outline: none;
        border: none;
        margin-right: 15px;
        color: white;
      }
      .btn-primary {
        background-color: var(--main-color);
        padding: 10px 20px;
        border-radius: 5px;
        font-size: 14px;
      }
      span {
        border-radius: 5px;
        font-size: 14px;
        display: flex;
        align-items: center;
        justify-content: center;
        font-size: 20px;
        cursor: pointer;
        background-color: rgba(170, 171, 190, 0.4);
        width: 35px;
        height: 35px;
      }
    }
  }
  .banner__next {
    position: absolute;
    right: 30px;
    top: 50%;
    transform: translateY(-50%);
    background-color: rgba(170, 171, 190, 0.4);
    padding: 5px;
    display: flex;
    align-items: center;
    justify-content: center;
    border-radius: 50%;
    cursor: pointer;
    i {
      font-size: 30px;
    }
  }
}
