.sidebarLeftItem {
  padding: 10px 0;

  .sidebarLeftItem__title {
    margin: 20px 0;
    color: var(--txt-color-two);
    font-size: 16px;
  }
  .sidebarLeftItem__content {
    color: var(--txt-color-one);
    margin-left: 5px;
    .sidebarLeftItem__content-link {
      color: var(--txt-color-one);
      .sidebarLeftItem__content-item {
        margin: 20px 0;
        display: flex;
        align-items: center;
        cursor: pointer;
        &:hover {
          color: var(--main-color);
        }
        i,
        span {
          margin: 0 5px;
        }
      }
    }
    .sidebarLeftItem__content-link.activeLink .sidebarLeftItem__content-item {
      color: var(--main-color);
      border-right: 2px solid var(--main-color);
    }
  }
}
