.row {
  margin: 30px 0;
  &__header {
    display: flex;
    align-items: center;
    justify-content: space-between;
    margin-bottom: 20px;
    &-title {
      font-size: 20px;
      font-weight: 500;
      color: var(--active-txt-color);
    }
    &-icon {
      i {
        color: var(--active-txt-color);
        padding: 10px;
        border-radius: 50%;
        border: 1px solid var(--txt-color-two);
        margin-left: 10px;
        font-size: 20px;
        cursor: pointer;
      }
    }
  }
  &__posters {
    // display: flex;
    // align-items: center;
    // overflow-x: scroll;
    // &::-webkit-scrollbar {
    //   display: none;
    // }
    &-item {
      cursor: pointer;
      border-radius: 15px;
      min-width: 150px;
      height: 200px;
      margin-right: 20px;
    }
  }
}
